<template>
    <div class="products">
        <div class="products__title">{{shopName}}</div>
        <ul class="product">
            <template
                v-for="item of productList"
                :key="item._id"
            >
                <li
                    class="product__item"
                    v-if="item.count > 0 && item.check"
                >
                    <img class="product__item__img" :src="item.imgUrl" />
                    <div class="product__item__detail">
                        <h4 class="product__item__detail__title">{{item.name}}</h4>
                        <p class="product__item__detail__price">
                            <span class="product__item__detail__price__yen">&yen;</span>
                            {{item.price}} × {{item.count}}
                            <span class="product__item__detail__price__total">
                                <span class="product__item__detail__price__yen">&yen;</span>
                                {{(item.price * item.count).toFixed(2)}}
                            </span>
                        </p>
                    </div>
                </li>
            </template>
        </ul>
    </div>
</template>

<script>
import { useRoute } from 'vue-router'
import { useCommonCartEffect } from '../../effects/cartEffects'

export default {
  name: 'ProductList',
  setup () {
    const route = useRoute()
    const shopId = route.params.id
    const { shopName, productList } = useCommonCartEffect(shopId)

    return { shopName, productList }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/viriables.scss';
@import '../../style/mixins.scss';
.products {
    // margin: .16rem .18rem .55rem .18rem;
    position: absolute;
    top: 2.12rem;
    left: .18rem;
    right: .18rem;
    bottom: .7rem;
    padding-bottom: .16rem;
    border-radius: .04rem;
    &__title {
        padding: .16rem 0 .24rem .16rem;
        line-height: .22rem;
        font-size: .16rem;
        color: $content-fontcolor;
        font-weight: bold;
        background: $bgColor;
        border-radius: .04rem;
    }
    .product {
        flex: 1;
        margin: 0;
        padding: 0;
        list-style: none;
        overflow-y: scroll;
        position: absolute;
        top: .5rem;
        left: 0;
        right: 0;
        bottom: 0;
        overflow-y: scroll;
        &__item {
            display: flex;
            position: relative;
            padding: 0 .16rem .16rem .16rem;
            background: $bgColor;
            &__img {
                width: .46rem;
                height: .46rem;
                margin-right: .16rem;
            }
            &__detail {
                flex: 1;
                height: .46rem;
                overflow: hidden;
                &__title {
                    margin: 0;
                    line-height: .2rem;
                    font-size: .14rem;
                    color: $content-fontcolor;
                    @include ellipsis;
                }
                &__price {
                    display: flex;
                    line-height: .2rem;
                    color: $hightLight;
                    font-size: .14rem;
                    margin: .06rem 0 0 0;
                    &__total {
                        flex: 1;
                        text-align: right;
                        color: $dark-fontColor;
                    }
                    &__yen {
                        font-size: .12rem;
                    }
                }
            }
        }
    }
}
</style>
