<template>
    <div class="order">
        <div class="order__price">实付金额 <b>¥{{calculations.price}}</b></div>
        <div class="order__btn" @click="() => handleSubmitClick(true)">提交订单</div>
    </div>
    <div
     class="mask"
     v-show="showConfirm"
     @click="() => handleSubmitClick(false)"
    >
        <div class="mask__content" @click.stop>
            <h3 class="mask__content__title">确认要离开收银台？</h3>
            <p class="mask__content__desc">请尽快完成支付，否则将被取消</p>
            <div class="mask__content__btns">
                <div
                 class="mask__content__btns__btn mask__content__btns__btn--first"
                 @click="() => handleConfirmOrder(true)"
                >取消订单</div>
                <div
                 class="mask__content__btns__btn mask__content__btns__btn--last"
                 @click="() => handleConfirmOrder(false)"
                >确认支付</div>
            </div>
        </div>
    </div>
    <Toast v-if="show" :message="toastMessage" />
</template>

<script>
import { ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from 'vuex'
import { post } from '../../utils/request'
import { useCommonCartEffect } from '../../effects/cartEffects'
import Toast, { useToastEffect } from '../../components/Toast'

// 提交订单相关逻辑
const useConfirmOrderEffect = () => {
  const router = useRouter()
  const route = useRoute()
  const store = useStore()
  const shopId = parseInt(route.params.id, 10)
  const { productList, shopName, calculations } = useCommonCartEffect(shopId)
  const { show, toastMessage, showToast } = useToastEffect()

  const products = []
  // eslint-disable-next-line prefer-const
  for (let key in productList.value) {
    const product = productList.value[key]
    if (product.count > 0 && product.check) {
      products.push({ id: parseInt(product._id), num: product.count })
    }
  }
  const handleConfirmOrder = async (isCanceled) => {
    try {
      const result = await post('/api/order', {
        addressId: 1,
        shopId,
        shopName: shopName.value,
        isCanceled,
        products
      })
      if (result?.errno === 0) {
        store.commit('clearCartProducts', { shopId })
        router.push({ name: 'OrderList' })
      } else {
        showToast('订单操作失败')
        setTimeout(() => {
          router.push({ path: `/shop/${shopId}` })
        }, 2000)
      }
    } catch (e) {
      showToast('网络请求失败')
      setTimeout(() => {
        router.push({ path: `/shop/${shopId}` })
      }, 2000)
    }
  }
  return { calculations, show, toastMessage, handleConfirmOrder }
}

// 处理蒙层操作
const useMaskEffect = () => {
  const showConfirm = ref(false)
  const handleSubmitClick = (state) => {
    showConfirm.value = state
  }
  return { showConfirm, handleSubmitClick }
}

export default {
  name: 'Order',
  components: { Toast },
  setup () {
    const { calculations, show, toastMessage, handleConfirmOrder } = useConfirmOrderEffect()
    const { showConfirm, handleSubmitClick } = useMaskEffect()

    return { calculations, show, toastMessage, showConfirm, handleConfirmOrder, handleSubmitClick }
  }
}
</script>

<style lang="scss" scoped>
@import '../../style/viriables.scss';
.order {
    position: fixed;
    left: 0;
    right: 0;
    bottom: 0;
    line-height: .49rem;
    display: flex;
    border-top: .01rem solid $wrapper-bgColor;
    background: $bgColor;
    &__price {
        flex: 1;
        color: $content-fontcolor;
        font-size: .14rem;
        padding-left: .24rem;
    }
    &__btn {
        width: .98rem;
        background: $btns-bgColor;
        color: $bgColor;
        text-align: center;
        font-size: .14rem;
    }
}
.mask {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0,0,0,0.50);
    &__content {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        width: 3rem;
        height: 1.6rem;
        background: $bgColor;
        border-radius: .04rem;
        text-align: center;
        &__title {
            line-height: .25rem;
            margin: .24rem 0 0 0;
            font-size: .18rem;
            color: $content-fontcolor;
        }
        &__desc {
            line-height: .2rem;
            margin: .08rem 0 0 0;
            font-size: .14rem;
            color: $medium-fontColor;
        }
        &__btns {
            display: flex;
            width: 1.84rem;
            margin: .24rem 0 0 .59rem;
            &__btn {
                width: .8rem;
                line-height: .32rem;
                border-radius: .16rem;
                font-size: .14rem;
                text-align: center;
                border: .01rem solid $btns-bgColor;
                &--first {
                    margin-right: .24rem;
                    color: $btn-bgColor;
                }
                &--last {
                    color: $bgColor;
                    background: $btns-bgColor;
                }
            }
        }
    }
}
</style>
